<template>
  <div id="recommended">
    <Breadcrumb :header-name="['']"></Breadcrumb>
    <div class="tabs">
      <h4 class="union_set"></h4>
      <el-form :label-position="labelPosition" label-width="100px" :model="form" :rules="rules" ref="form">
        <el-form-item label=":" prop="busUserName">
          <el-col :span="6">
            <el-input v-model="form.busUserName" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="enterpriseName">
          <el-col :span="6">
            <el-input v-model="form.enterpriseName" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="directorName">
          <el-col :span="6">
            <el-input v-model="form.directorName" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="directorPhone">
          <el-col :span="6">
            <el-input v-model="form.directorPhone" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="directorEmail">
          <el-col :span="6">
            <el-input v-model="form.directorEmail" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="reason">
          <el-input type="textarea" :rows="4" id="feedbackcontent" placeholder="" v-model="form.reason" :maxlength="unionNoticeMaxlength" @focus="unionNoticeFocus" @blur="unionNoticeBlur" @change="unionNoticeKeydown($event)" @keydown.native="unionNoticeKeydown($event)" @keyup.native="unionNoticeKeydown($event)" @input.native="unionNoticeKeydown($event)" @onpropertychange.native="unionNoticeKeydown($event)"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit('form')"></el-button>
          <el-button @click="back"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import $http from '@/utils/http.js';
import Breadcrumb from '@/components/public-components/Breadcrumb';
export default {
  name: 'union-recommend',
  components: {
    Breadcrumb
  },
  data() {
    // 
    let directorPhonePass = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''));
      } else if (!value.match(/(^1[3|4|5|6|7|8][0-9][0-9]{8}$)|(^0\d{2,3}-?\d{7,8}$)/)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    let emailPass = (rule, value, callback) => {
      if (!value) {
        callback(new Error(''));
      } else if (!value.match(/^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    return {
      labelPosition: 'right',
      form: {},
      rules: {
        busUserName: [{ required: true, message: '', trigger: 'blur' }],
        enterpriseName: [{ required: true, message: '', trigger: 'blur' }],
        directorName: [{ required: true, message: '', trigger: 'blur' }],
        directorPhone: [{ validator: directorPhonePass, trigger: 'blur' }],
        directorEmail: [{ validator: emailPass, trigger: 'blur' }],
        reason: [{ required: true, message: '', trigger: 'blur' }]
      },
      unionNoticeMaxlength: 40,
      checkList: ['busUserName'],
      datas: []
    };
  },
  computed: {
    unionId() {
      return this.$store.state.unionId;
    },
    isUnionOwner() {
      return this.$store.state.isUnionOwner;
    }
  },
  mounted: function() {
    // 
    $http
      .get(`/unionMain/${this.unionId}`)
      .then(res => {
        if (res.data.data) {
          let checkList_ = res.data.data.itemList;
          checkList_.forEach((v, i) => {
            if (this.checkList.indexOf(v.itemKey) === -1) {
              this.checkList.push(v.itemKey);
            }
          });
          for (let key in this.rules) {
            if (this.checkList.indexOf(key) === -1) {
              this.rules[key] = [];
            }
          }
        }
      })
      .catch(err => {
        this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
      });
  },
  methods: {
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let url = `/unionMemberJoin/unionId/${this.unionId}/type/2`;
          // 
          let data = {};
          data = this.form;
          $http
            .post(url, data)
            .then(res => {
              if (res.data.success) {
                this.$message({ showClose: true, message: '', type: 'success', duration: 5000 });
                this.form = {};
                if (this.isUnionOwner) {
                  eventBus.$emit('unionUpdata');
                }
              }
            })
            .catch(err => {
              this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
            });
        } else {
          return false;
        }
      });
    },
    back() {
      this.$router.push({ path: '/my-union/index' });
    },
    // 
    unionNoticeFocus() {
      let valueLength = this.unionNoticeCheck()[0];
      let len = this.unionNoticeCheck()[1];
      if (valueLength > 20) {
        this.form.reason = this.form.reason.substring(0, len + 1);
        this.unionNoticeMaxlength = len;
        return false;
      } else {
        this.unionNoticeMaxlength = 40;
      }
    },
    unionNoticeBlur() {
      let valueLength = this.unionNoticeCheck()[0];
      if (valueLength > 20) {
        return false;
      }
    },
    unionNoticeKeydown(e) {
      let lengthObj = this.unionNoticeCheck();
      let valueLength = lengthObj[0];
      let len = lengthObj[1];
      if (valueLength > 20) {
        // this.form.reason = this.form.reason.substring(0, len + 1);
        this.unionNoticeMaxlength = len;
        return false;
      } else {
        this.unionNoticeMaxlength = 40;
      }
    },
    unionNoticeCheck() {
      let valueLength = 0;
      let maxLenth = 0;
      let chinese = '[\u4e00-\u9fa5]'; // 21
      let str = this.form.reason || '';
      for (let i = 0; i < str.length; i++) {
        // 
        let temp = str.substring(i, i + 1); // 
        if (temp.match(chinese)) {
          // 1
          valueLength += 1;
        } else {
          // 0.5
          valueLength += 0.5;
        }
        if (Math.ceil(valueLength) == 20) {
          maxLenth = i;
        }
      }
      valueLength = Math.ceil(valueLength); //
      return [valueLength, maxLenth];
    }
  }
};
</script>
<style lang='less' rel="stylesheet/less" scoped>
.tabs {
  margin: 40px 50px;
}
#feedbackcontent::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #97a8be;
}
#feedbackcontent:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #97a8be;
}
#feedbackcontent::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #97a8be;
}
#feedbackcontent:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #97a8be;
}
</style>



