<template>
  <div>
    <div>
      <p style="text-align: right;color:#20A0FF">
        <span @click="visible1=true" style="cursor: pointer">?</span>
      </p>
      <el-table :data="tableData" style="margin-top: 25px">
        <el-table-column prop="member.enterpriseName" label="">
        </el-table-column>
        <el-table-column prop="memberOut.createTime" label="">
        </el-table-column>
        <el-table-column prop="memberOut.applyOutReason" label="">
        </el-table-column>
        <el-table-column prop="" label="" width="150">
          <template slot-scope="scope">
            <div class="sizeAndColor">
              <el-button size="small" @click="agree(scope)"></el-button>
              <el-button size="small" @click="disagree(scope)"></el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="footer">
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalAll" v-if="tableData.length>0">
      </el-pagination>
    </div>
    <!--  -->
    <div class="tuimeng">
      <el-dialog title="" :visible.sync="visible1" size="tiny">
        <hr>
        <div class="model_12">
          <p>1. </p>
          <p>2. 
            <p>3. </p>
            <p>4. </p>
            <p>5. </p>
            <p>6. </p>
            <p>7. 15</p>
            <p>8. </p>
            <p>&nbsp;&nbsp;&nbsp;&nbsp;15</p>
            <p>9. </p>
            <p>10. </p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="visible1=false"> </el-button>
        </span>
      </el-dialog>
    </div>
    <!--   -->
    <div class="tuimeng">
      <el-dialog title="" :visible.sync="visible2" size="tiny">
        <div class="model_12">
          <p>{{ outEnterpriseName }}?</p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="confirm2"></el-button>
          <el-button @click="visible2=false"></el-button>
        </span>
      </el-dialog>
      <el-dialog title="" :visible.sync="visible3" size="tiny">
        <div class="model_12">
          <p>{{ outEnterpriseName }}</p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="confirm3"></el-button>
          <el-button @click="visible3=false"></el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import $http from '@/utils/http.js';
export default {
  name: 'union-quit-check',
  data() {
    return {
      visible1: false,
      tableData: [],
      currentPage: 1,
      totalAll: '',
      visible2: false,
      visible3: false,
      outEnterpriseName: '',
      outId: ''
    };
  },
  computed: {
    unionId() {
      return this.$store.state.unionId;
    }
  },
  mounted: function() {
    eventBus.$on('newOutApply', () => {
      this.init();
    });
    this.init();
  },
  methods: {
    init() {
      $http
        .get(`/unionMemberOut/unionId/${this.unionId}/page?current=1`)
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.records || [];
            this.totalAll = res.data.data.total;
          } else {
            this.tableData = [];
            this.totalAll = '';
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    handleCurrentChange(val) {
      $http
        .get(`/unionMemberOut/unionId/${this.unionId}/page?current=${val}`)
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.records || [];
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    //  
    agree(scope) {
      this.visible2 = true;
      this.outId = scope.row.memberOut.id;
      this.outEnterpriseName = scope.row.member.enterpriseName;
    },
    //  
    disagree(scope) {
      this.visible3 = true;
      this.outId = scope.row.memberOut.id;
      this.outEnterpriseName = scope.row.member.enterpriseName;
    },
    //   
    confirm2() {
      $http
        .put(`/unionMemberOut/${this.outId}/unionId/${this.unionId}?isPass=1`)
        .then(res => {
          if (res.data.success) {
            this.visible2 = false;
            $http
              .get(`/unionMemberOut/unionId/${this.unionId}/page?current=1`)
              .then(res => {
                if (res.data.data) {
                  this.tableData = res.data.data.records || [];
                  this.totalAll = res.data.data.total;
                }
              })
              .catch(err => {
                this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
              });
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    //   
    confirm3() {
      $http
        .put(`/unionMemberOut/${this.outId}/unionId/${this.unionId}?isPass=0`)
        .then(res => {
          if (res.data.success) {
            this.visible3 = false;
            $http
              .get(`/unionMemberOut/unionId/${this.unionId}/page?current=1`)
              .then(res => {
                if (res.data.data) {
                  this.tableData = res.data.data.records || [];
                  this.totalAll = res.data.data.total;
                }
              })
              .catch(err => {
                this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
              });
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    }
  }
};
</script>




