<template>
  <div id="un_set">
    <p class="union_set"></p>
    <div class="second_">
      <el-form :model="form" :rules="rules" ref="form" label-width="200px" style="margin-left: -68px;">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.joinType">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-switch on-text="" off-text="" v-model="form.isIntegral" :disabled="isIntegral_"></el-switch>
          <el-tooltip content="" placement="right">
            <span class="tubiao">!</span>
          </el-tooltip>
        </el-form-item>
        <el-form-item label="/" class="lianmeng">
          <el-checkbox-group v-model="checkList">
            <el-checkbox label="enterpriseName" disabled></el-checkbox>
            <el-checkbox label="directorName"></el-checkbox>
            <el-checkbox label="directorPhone" disabled></el-checkbox>
            <el-checkbox label="directorEmail"></el-checkbox>
            <el-checkbox label="reason">/</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="">
          <img v-if="this.form.img" v-bind:src="this.form.img" class="unionImg" @click="materiallayer">
          <el-button @click="materiallayer" v-if="!this.form.img">
            <i class="el-icon-plus"></i>
          </el-button>
          <span style="position: relative;top: -1px;left: 6px;color:#bbbbbb;" v-if="!this.form.unionImg">100*100px</span>
          <el-dialog :visible.sync="materialVisible" title="">
            <hr>
            <iframe :src="materialUrl" width="95%" height="500" frameborder="0" scrolling="no"></iframe>
          </el-dialog>
        </el-form-item>
        <el-form-item label="" prop="illustration">
          <el-input type="textarea" :rows="3" placeholder="" v-model="form.illustration">
          </el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="footer_public">
      <el-button type="primary" @click="submitForm('form')"></el-button>
      <el-button @click="back"></el-button>
    </div>
  </div>
</template>

<script>
import $http from '@/utils/http.js';
export default {
  name: 'union-setting-owner',
  data() {
    return {
      childrenData: '',
      form: {
        name: '',
        img: '',
        joinType: 2,
        isIntegral: true,
        illustration: ''
      },
      checkList: ['enterpriseName', 'directorPhone'],
      rules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        img: [{ required: true, message: '', trigger: 'change' }],
        illustration: [{ required: true, message: '', trigger: 'blur' }]
      },
      materialVisible: false,
      materialUrl: '',
      isIntegral_: false // 
    };
  },
  computed: {
    unionId() {
      return this.$store.state.unionId;
    }
  },
  watch: {},
  mounted: function() {
    let _this = this;
    window.addEventListener('message', function(e) {
      if (e.data && e.data != 'go_back()') {
        _this.form.unionImg = e.data.split(',')[1].replace(/\'|\)/g, '');
      }
      _this.materialVisible = false;
    });
    this.init();
    eventBus.$on('unionSettingTabChange', () => {
      this.init();
    });
  },
  methods: {
    init() {
      // 
      $http
        .get(`/unionMain/${this.unionId}`)
        .then(res => {
          if (res.data.data) {
            this.form.name = res.data.data.union.name;
            this.form.joinType = res.data.data.union.joinType;
            this.form.isIntegral = Boolean(res.data.data.union.isIntegral);
            this.isIntegral_ = Boolean(res.data.data.union.isIntegral);
            this.form.illustration = res.data.data.union.illustration;
            this.form.img = res.data.data.union.img;
            if (res.data.data.itemList) {
              res.data.data.itemList.forEach((v, i) => {
                if (this.checkList.indexOf(v.itemKey) === -1) {
                  this.checkList.push(v.itemKey);
                }
              });
            }
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    materiallayer() {
      this.materialVisible = true;
      this.materialUrl = this.$store.state.materialUrl + window.location.href;
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let url = `/unionMain/${this.unionId}`;
          // 
          let data = {};
          data.union = {};
          data.union.isIntegral = this.form.isIntegral - 0;
          data.union.joinType = this.form.joinType - 0;
          data.union.illustration = this.form.illustration;
          data.union.img = this.form.img;
          data.union.name = this.form.name;
          data.itemList = [];
          this.checkList.forEach((v, i) => {
            data.itemList.push({ itemKey: v, unionId: this.unionId });
          });
          $http
            .put(url, data)
            .then(res => {
              if (res.data.success) {
                eventBus.$emit('unionUpdata');
                this.$message({ showClose: true, message: '', type: 'success', duration: 5000 });
              }
            })
            .catch(err => {
              this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
            });
        } else {
          return false;
        }
      });
    },
    back() {
      this.$router.push({ path: '/my-union/index' });
    }
  }
};
</script>

<style lang='less' rel="stylesheet/less" scoped>
#un_set {
  form {
    margin-left: -70px;
  }
}

.unionImg {
  width: 80px;
  height: 80px;
}
/**/
.tubiao {
  width: 18px;
  height: 18px;
  border: 1px solid #1c8de0;
  border-radius: 50%;
  display: block;
  text-align: center;
  line-height: 18px;
  color: #1d90e6;
  font-weight: bold;
  position: absolute;
  top: 9px;
  left: 55px;
  cursor: pointer;
}
</style>



